.resetmp {
    margin: 0;
    padding: 0;
}

.g-pd-y {
    padding-top: 30px;
    padding-bottom: 30px;
}

.primary-gradient {
    background: rgb(157, 107, 240);
    background: -moz-linear-gradient(0deg, rgba(157, 107, 240, 1) 0%, rgba(135, 90, 238, 1) 50%, rgba(143, 85, 238, 1) 100%);
    background: -webkit-linear-gradient(0deg, rgba(157, 107, 240, 1) 0%, rgba(135, 90, 238, 1) 50%, rgba(143, 85, 238, 1) 100%);
    background: linear-gradient(0deg, rgba(157, 107, 240, 1) 0%, rgba(135, 90, 238, 1) 50%, rgba(143, 85, 238, 1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#9d6bf0", endColorstr="#8f55ee", GradientType=1);
}

.secondary-gradient {
    background: rgb(41, 41, 41);
    background: -moz-linear-gradient(0deg, rgba(41, 41, 41, 1) 0%, rgba(55, 55, 55, 1) 50%, rgba(27, 27, 27, 1) 100%);
    background: -webkit-linear-gradient(0deg, rgba(41, 41, 41, 1) 0%, rgba(55, 55, 55, 1) 50%, rgba(27, 27, 27, 1) 100%);
    background: linear-gradient(0deg, rgba(41, 41, 41, 1) 0%, rgba(55, 55, 55, 1) 50%, rgba(27, 27, 27, 1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#292929", endColorstr="#1b1b1b", GradientType=1);
}

.team-gradient {
    background: rgb(136, 87, 238);
    background: -moz-linear-gradient(0deg, rgba(136, 87, 238, 1) 0%, rgba(159, 119, 242, 1) 50%, rgba(136, 87, 238, 1) 100%);
    background: -webkit-linear-gradient(0deg, rgba(136, 87, 238, 1) 0%, rgba(159, 119, 242, 1) 50%, rgba(136, 87, 238, 1) 100%);
    background: linear-gradient(0deg, rgba(136, 87, 238, 1) 0%, rgba(159, 119, 242, 1) 50%, rgba(136, 87, 238, 1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#8857ee", endColorstr="#8857ee", GradientType=1);
}

.myButton {
    font-family: "Barlow-Med";
    text-transform: uppercase;
    color: @light-font-color;
    border: 2px solid @light-font-color;
    .primary-gradient;
    box-shadow: 1px 2px 4px lighten(black, 25%);

    &:hover {
        .secondary-gradient;
    }
}