section.contactUs {
    .g-pd-y;
    text-align: center;
    overflow-x: hidden;

    header {
        text-align: center;

        h1 {
            font-size: calc(32px + (38 - 32) * ((100vw - 300px) / (1366 - 300)));
        }

        p {
            font-size: calc(14px + (16 - 14) * ((100vw - 300px) / (1366 - 300)));
        }
    }


    .contact-wrap {
        .g-pd-y;
        background-image: url("../images/contactIMG.png");
        background-repeat: no-repeat;
        background-size: 900px;
        background-position: center;

        @media (min-width: 992px) {
            height: 100vh;
            background-size: contain;
        }

        // position: absolute;
        // top: 0;
        // left: 0;
        .links-wrap {
            .g-pd-y;
            border: 1px solid #c1c1c1;
            background-color: #efefefab;

            @media (min-width: 992px) {
                background-color: #efefef;
            }

            .contact-bar {
                display: flex;
                width: 100%;
                justify-content: center;
                align-items: center;
                padding: 10px 0 !important;
                // margin-bottom: 20px;
                margin: 10px 0;

                img {
                    width: 55px;
                    margin-right: 10px;
                }

                a {
                    display: block;
                    color: @dark-font-color;
                    font-family: "Karla-Reg";
                    font-size: 20px;
                    margin-left: 10px;
                    transition: color .5s;
                    text-align: left;
                }
            }


        }

        form {
            margin-top: 20px;

            @media (min-width: 992px) {
                margin-top: 0;
            }

            input {
                font-family: "Barlow-Reg";
                width: 100%;
                padding: 15px;
                color: @dark-font-color;
                border: 1px solid #c1c1c1;
                margin-bottom: 15px;
                font-size: 16px;
                background-color: #efefefab;

                @media (min-width: 992px) {
                    background-color: #efefef;
                }

                &::placeholder {
                    text-transform: uppercase;
                    font-size: 13px;
                }
            }

            textarea {
                resize: none;
                font-family: "Barlow-Reg";
                width: 100%;
                padding: 15px;
                color: @dark-font-color;
                border: 1px solid #c1c1c1;
                margin-bottom: 15px;
                font-size: 14px;
                background-color: #efefefab;

                @media (min-width: 992px) {
                    background-color: #efefef;
                }

                &::placeholder {
                    text-transform: uppercase;
                    font-size: 13px;
                }
            }

            button {
                .myButton;
                width: 100%;
                // background-color: @primary-color;
                color: @light-font-color;
                padding: 12px 0;
                font-size: 18px;
                // text-shadow: 2.5px 4.33px 10px rgba(0, 0, 0, 0.75);
            }
        }
    }
}