.sidebar-wrapper {
    nav#sidebar {
        .g-pd-y;
        position: fixed;
        z-index: 99999;
        left: -300px;
        // left: 0;
        top: 0;
        .primary-gradient;
        width: 300px;
        height: 100vh;
        transition: .4s;
        box-shadow: 2px 3px 5px rgba(0, 0, 0, 0.7);

        .closeSideBar {
            position: absolute;
            top: 5px;
            right: 5px;
            cursor: pointer;
            z-index: 22;

            img {
                width: 30px;
            }
        }

        .navbar-brand {
            display: block;
            text-align: center;

            .logo-img {
                width: 100px;
            }
        }

        nav.menu {
            .g-pd-y;
            text-align: center;

            .nav-item {
                margin: 10px 0;

                .nav-link {
                    transition: .3s;
                    font-family: "Abril-Reg";
                    color: @light-font-color;
                    font-size: 28px;
                    text-transform: capitalize;
                    letter-spacing: 1.5px;
                }

                &:hover {
                    .nav-link {
                        color: lighten(@dark-font-color, 15%);
                    }
                }
            }

        }

        .nav-item.active {
            .nav-link {
                color: lighten(@dark-font-color, 15%) !important;
            }
        }
    }

    .overlay {
        display: none;
        position: fixed;
        /* full screen */
        width: 100vw;
        height: 100vh;
        /* transparent black */
        background: rgba(0, 0, 0, 0.7);
        /* middle layer, i.e. appears below the sidebar */
        z-index: 998;
        opacity: 0;
        /* animate the transition */
        transition: all 0.5s ease-in-out;
    }

    /* display .overlay when it has the .active class */
    .overlay.active {
        display: block;
        opacity: 1;
    }
}

.slide {
    left: 0 !important;
}