section.whyyoga {
    .g-pd-y;

    .left-section {
        position: relative;
        text-align: center;

        .yoga-img {
            margin-top: 80px;
            width: 95%;
            filter: drop-shadow(1px 1px 10px lighten(black, 65%));
        }

        .back-img {
            position: absolute;
            width: 67%;
            left: 50%;
            top: 10px;
            transform: translateX(-52%);
            z-index: -1;
            filter: drop-shadow(3px 4px 2px lighten(black, 60%));
        }
    }

    .right-section {
        .g-pd-y;

        h1 {
            // font-size: 38px;
            font-size: calc(34px + (38 - 34) * ((100vw - 300px) / (1366 - 300)));
        }

        p {
            font-size: calc(14px + (18 - 14) * ((100vw - 300px) / (1366 - 300)));
        }

        .yoga-benefit-bar {
            display: flex;
            // justify-content: center;
            align-items: center;
            margin-bottom: 15px;

            .img-wrap {
                width: 60px;
                height: 60px;
                .secondary-gradient;
                display: flex;
                justify-content: center;
                align-items: center;
                border: 2px solid @light-font-color;
                box-shadow: 3px 3px 3px lighten(@dark-font-color, 60%);

                img {
                    width: 40px;
                    filter: drop-shadow(1px 1px 1px black);
                }
            }

            h3 {
                margin-left: 15px;
                font-size: calc(18px + (20 - 18) * ((100vw - 300px) / (1366 - 300)));
            }

            &:hover {
                .img-wrap {
                    .primary-gradient;
                }
            }
        }
    }
}