section.hero {
    .hero-wrap {
        height: 100vh;
        background-position: center;
        background-size: cover;
        position: relative;
        z-index: 0;

        @media (orientation: landscape) {
            height: 130vh;
        }

        @media only screen and (orientation: portrait) and (min-width: 992px) {
            height: 130vh;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: 60% center;

            @media (min-width: 992px) {
                object-position: center;
            }
        }

        .owlOne {
            position: absolute;
            top: 40%;
            left: 0;
            transform: translateY(-30%);

            .item {
                padding: 10px 0;
                // height: 100%;

                h1 {
                    // font-size: 40px;
                    font-size: calc(24px + (45 - 24) * ((100vw - 300px) / (1366 - 300)));
                }

                p {
                    font-family: "Barlow-Med";
                    color: @light-font-color;
                    font-size: calc(14px + (18 - 14) * ((100vw - 300px) / (1366 - 300)));
                    text-shadow: 1px 1px 1px lighten(@dark-font-color, 20%);
                }

                .btn-g {
                    display: flex;

                    a {
                        .myButton;
                        border-radius: 20px;
                        margin-top: 10px !important;
                        padding: 10px 25px;
                        margin-right: 10px;
                    }
                }
            }
        }
    }
}