body,
html {
    .resetmp;
    -moz-user-select: none;
    -ms-user-select: none;
    -webkit-user-select: none;
    user-select: none;

    main {
        .simplebar-scrollbar {
            width: 3px;

            &::before {
                background: @light-font-color;
            }
        }
    }

    footer {
        height: 80px;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: black;
        margin-top: 20px;

        p {
            .resetmp;
            text-align: center;
            font-family: "Barlow-Reg";
            font-size: 18px;
            color: @light-font-color;
            text-transform: uppercase;
        }
    }
}

.lock-scroll{
    overflow: hidden;
}