header.topnav {
    // background-color: yellow;
    padding: 15px 0;
    position: fixed;
    z-index: 99999;
    top: 0;
    left: 0;
    width: 100%;

    .navbar-brand {
        padding-left: 15px;

        img {
            width: 150px;
        }
    }

    .nav-link {
        img {
            width: 40px;
        }
    }
}

.nav-color {
    background-color: @primary-solid-color;
    transition: .2s;
}