p {
    font-family: "Barlow-Reg";
    color: @dark-font-color;
    letter-spacing: .5px;
    line-height: 28px;
}

a,button {
    text-decoration: none !important;
    outline: none;
    border: none;

    &:focus {
        outline: none;
        border: none;
    }
}

h1 {
    font-family: "Abril-Reg";
    color: @primary-solid-color;
    letter-spacing: .5px;
    text-transform: capitalize;
    text-shadow: 1px 1px 1px lighten(@dark-font-color, 20%);
}

h3 {
    font-family: "Barlow-SemiBold";
    color: @dark-font-color;
    text-transform: uppercase;
}

h4 {
    font-family: "Abril-Reg";
    color: @light-font-color;
    letter-spacing: .5px;
    text-transform: capitalize;
}