section.stances {
    .g-pd-y;
    padding-bottom: 50px;
    background: linear-gradient(rgba(255, 255, 255, .8),
        rgba(255, 255, 255, .8)), url("../images/stancesBG.png");
    background-size: cover;

    header {
        text-align: center;

        h1 {
            font-size: calc(32px + (38 - 32) * ((100vw - 300px) / (1366 - 300)));
        }

        p {
            font-size: calc(14px + (16 - 14) * ((100vw - 300px) / (1366 - 300)));
        }
    }

    .yoga-stance-items {
        .stance-item-wrap {
            .secondary-gradient;
            text-align: center;
            padding: 20px 10px;
            border: 2px solid @light-font-color;
            box-shadow: 2px 3px 4px lighten(black, 40%);
            margin-bottom: 20px;

            img {
                width: 70px;
                filter: drop-shadow(1px 1px 1px black);
                margin-bottom: 10px;
            }

            h4 {
                font-size: calc(19px + (22 - 19) * ((100vw - 300px) / (1366 - 300)));
            }

            .detail-text {
                .resetmp;
                height: 90px;

                p {
                    .resetmp;
                    font-weight: lighter;
                    color: @light-font-color;
                    font-size: 14px;
                    // font-size: calc(13px + (14 - 13) * ((100vw - 300px) / (1366 - 300)));
                }

                @media only screen and (orientation: landscape) {
                    height: 115px;
                }

                @media only screen and (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : portrait) {
                    height: 120px;
                }

                @media (min-width:992px) {
                    height: 90px;
                }

            }

            .readMore {
                font-family: "Barlow-Med";
                text-transform: uppercase;
                color: @light-font-color;
                background: transparent;
                font-size: 15px;
            }

            &:hover {
                .primary-gradient;
            }
        }
    }

}