section.gallery {
    .g-pd-y;

    header {
        text-align: center;

        h1 {
            font-size: calc(32px + (38 - 32) * ((100vw - 300px) / (1366 - 300)));
        }

        p {
            font-size: calc(14px + (16 - 14) * ((100vw - 300px) / (1366 - 300)));
        }
    }

    .images-gallery {
        .img-wrap {
            display: block;
            position: relative;
            height: 100%;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }

            &::before {
                content: "";
                display: block;
                background-color: @primary-solid-color;
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                opacity: .7;
                transform: scale(0);
                transition: .3s;
            }

            &::after {
                content: url("../images/Eye.png");
                display: block;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%) scale(0);
                filter: drop-shadow(3px 3px 2px black);
                transition: .3s;
            }

            &:hover {
                &::before {
                    transform: scale(1);
                }

                &::after {
                    transform: translate(-50%, -50%) scale(.55);

                    @media (min-width: 992px) {
                        transform: translate(-50%, -50%) scale(.9);
                    }
                }
            }
        }
    }
}