section.team {
    .g-pd-y;

    header {
        text-align: center;

        h1 {
            font-size: calc(32px + (38 - 32) * ((100vw - 300px) / (1366 - 300)));
        }

        p {
            font-size: calc(14px + (16 - 14) * ((100vw - 300px) / (1366 - 300)));
        }
    }

    .team-slider {
        .owlTwo {
            .item {
                .team-wrap {
                    border: 3px solid @dark-font-color;
                    position: relative;
                    z-index: 99;

                    .label-wrap {
                        .resetmp;
                        text-align: center;
                        position: absolute;
                        bottom: -80px;
                        left: 0;
                        // z-index: 1;
                        width: 100%;
                        transition: .4s;

                        h4 {
                            .resetmp;
                            padding: 10px 0;
                            width: 100%;
                            .team-gradient;
                        }
                    }

                    .detail-overlay {
                        position: absolute;
                        top: -100%;
                        left: 50%;
                        transform: translateX(-50%);
                        width: 90%;
                        background-color: rgba(255, 255, 255, 0.8);
                        border: 3px solid black;
                        padding: 30px 20px;
                        transition: .4s;

                        h4 {
                            color: @primary-solid-color;
                            font-size: 24px
                        }

                        .detail-text {
                            .resetmp;
                            height: 100px;

                            .simplebar-scrollbar {
                                width: 4px;

                                &::before {
                                    background: @primary-solid-color;
                                }
                            }

                            p {
                                .resetmp;
                                font-family: "Barlow-Med";
                                line-height: normal;
                                // font-size: 16px;
                                font-size: calc(13px + (16 - 13) * ((100vw - 300px) / (1366 - 300)));
                            }

                        }

                        @media only screen and (orientation: landscape) {
                            .detail-text {
                                height: 120px;
                            }
                        }

                        @media only screen and (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : landscape) {
                            .detail-text {
                                height: 100px !important;
                            }
                        }

                        @media (min-width:992px) {
                            .detail-text {
                                height: 140px;
                            }
                        }
                    }

                    .readTMore {
                        font-family: "Barlow-SemiBold";
                        text-transform: uppercase;
                        color: @primary-solid-color;
                        background: transparent;
                        font-size: 15px;
                        cursor: pointer;
                    }
                }

                &:hover {
                    .team-wrap {
                        border-color: @primary-solid-color;

                        .label-wrap {
                            bottom: 0;
                        }

                        .detail-overlay {
                            top: 5%;
                        }
                    }
                }
            }

            .owl-dots {
                margin-top: 40px !important;
                display: none;

                .owl-dot {
                    span {
                        padding: 10px;
                        border-radius: 0;
                        .secondary-gradient;
                        border: 2px solid @primary-solid-color;
                        box-shadow: 3px 3px 3px lighten(@primary-solid-color, 15%);

                        &:hover {
                            .primary-gradient;
                            border: 2px solid @dark-font-color;
                        }
                    }

                }

                .active {
                    span {
                        .primary-gradient;
                        border: 2px solid @dark-font-color;
                    }
                }

                @media (min-width: 992px) {
                    display: block;

                    .owl-dot {
                        span {
                            // padding: 9px !important;
                        }
                    }
                }
            }
        }
    }
}